  font-display: swap;
  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1;
    display: flex;
    flex-direction: column; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.desktop-hidden {
  display: none; }
  @media (max-width: 1023px) {
    .desktop-hidden {
      display: block; } }

@media (max-width: 1023px) {
  .table-hidden {
    display: none; } }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 34px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

.header {
  display: block; }

.header__wrapper {
  padding: 25px 0; }
  @media (max-width: 1023px) {
    .header__wrapper {
      padding-bottom: 19px; } }

.preloader {
  display: block;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: #ffffff;
  z-index: 1;
  opacity: 1;
  transition: opacity 0.6s ease-in; }
  .preloader--opacity {
    opacity: 0; }
  .preloader--hide {
    display: none; }

.preloader__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  padding-bottom: 100px; }

.preloader__logo {
  margin-top: auto;
  flex-shrink: 0;
  width: 801px;
  height: 100px;
  transition: all 0.6s ease-in;
  left: 0;
  top: 0; }
  .preloader__logo--animate {
    position: relative;
    left: -45%;
    top: -42%;
    width: 192px;
    height: 24px; }
  @media (max-width: 1023px) {
    .preloader__logo {
      width: 550px;
      height: 70px; }
      .preloader__logo--animate {
        left: -36%;
        top: -45%;
        width: 192px;
        height: 24px; } }

.preloader__text {
  margin-top: auto;
  margin-bottom: 0;
  flex-shrink: 0;
  bottom: 0;
  transition: bottom 0.6s ease-in; }
  .preloader__text span {
    font: inherit; }
  .preloader__text--animate {
    position: relative;
    bottom: -50%; }

.main-page {
  position: relative;
  flex-grow: 1;
  padding-top: 43px;
  overflow: hidden; }
  @media (max-width: 1023px) {
    .main-page {
      padding-top: 0; } }

.main-page__layout {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  background-image: url("../img/svg/icon-a.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  max-width: 960px;
  margin: 0 auto;
  padding-top: 56px; }
  @media (max-width: 1023px) {
    .main-page__layout {
      background-image: none;
      padding-top: 0; } }

.main-page__title {
  font-weight: normal;
  font-size: 96px;
  line-height: 1;
  color: #2c39f2;
  margin-top: 0;
  margin-bottom: 0; }

.main-page__col-1 {
  width: 60%;
  flex-shrink: 0;
  transform: translate(0);
  transition: transform 0.4s ease-out; }
  .main-page__col-1--animation {
    transform: translate(0, 30%); }
  @media (max-width: 1023px) {
    .main-page__col-1 {
      width: 100%;
      margin-top: 234px; } }

.main-page__col-2 {
  width: 40%;
  flex-shrink: 0;
  transform: translate(0);
  transition: transform 0.4s ease-out; }
  .main-page__col-2--animation {
    transform: translate(0, -30%); }
  @media (max-width: 1023px) {
    .main-page__col-2 {
      position: absolute;
      left: -100%;
      width: 100%;
      top: 0;
      bottom: 0;
      background-color: #2c39f2;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      transition: left 0.4s ease-out;
      overflow: auto;
      padding-bottom: 40px;
      padding-top: 56px; }
      .main-page__col-2--show {
        left: 0; } }

.main-page__dino-box {
  margin-bottom: 32px; }

.main-page__text {
  font-weight: normal;
  font-size: 30px;
  line-height: 1.3;
  margin: 0;
  margin-left: 28px; }
  @media (max-width: 1023px) {
    .main-page__text {
      font-weight: normal;
      font-size: 30px;
      line-height: 1.3;
      color: #ffffff;
      margin-left: 0;
      width: 352px; } }

.dino {
  height: 230px;
  width: 316px;
  position: relative; }

.dino__rex {
  bottom: 0;
  left: 41%;
  position: absolute; }

.lightning {
  position: absolute; }
  .lightning--1 {
    top: 54px;
    left: 11%; }
  .lightning--2 {
    left: 36%;
    width: 40px;
    height: 70px;
    transform: rotate(18deg);
    top: 25px; }
  .lightning--3 {
    left: 62%;
    width: 101px;
    height: 174px;
    transform: rotate(59deg);
    top: -60px; }
  .lightning--4 {
    width: 40px;
    height: 69px;
    left: 74%;
    transform: rotate(79deg);
    top: 130px; }
  .lightning--purple {
    fill: #9627a3; }
  .lightning--pink {
